.btn {
    background: #f44336;
    border-radius: 25px;
    border: none;
    height: 36px;
    display: flex;
    align-items: center;
    padding: 0 16px;
    color: #fff;
    letter-spacing: 1.25px;
    text-transform: uppercase;
    cursor: pointer;
    box-shadow: 0 1px 5px rgba(244,67,54,.2), 0 3px 4px rgba(244,67,54,.12), 0 2px 4px rgba(244,67,54,.14);
    &:hover {
        background: #db4238; }
    @media screen and ( max-width: 768px ) {
        font-size: 0.5rem; }
    &--dark {
        background: #8c949f;
        box-shadow: 0 1px 5px rgba(43,59,77,.2), 0 3px 4px rgba(43,59,77,.12), 0 2px 4px rgba(43,59,77,.14);
        &:hover {
            background: #808995; } } }
